* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
 
}

.card {
  align-items: center;
  border: 1px solid grey;
  border-radius: 7px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 379px;
  
}

.card p {
  padding: 15px;
  text-align: center;
  
}

.card img {
  max-height: 300px;
}